import React from "react";
import PropTypes from "prop-types";
import withStyles from "@material-ui/core/styles/withStyles";
import Star from "@cvccorp-components/icons/dist/Star";
import { styles } from "./styles";

function Stars({ classes, id, className, onClick, size, stars, disabled }) {
    return (
        <span id={id} className={className} onClick={onClick} data-stars={stars}>
            {Array.from({ length: 5 }, (_, idx) => (
                <Star size={size ?? "medium"} noGutter fill={stars >= (idx + 1)}
                    className={disabled ? classes.disabled : stars >= (idx + 1) ? classes.filled : classes.empty}
                />
            ))}
        </span>
    );
}

Stars.propTypes = {
    classes: PropTypes.object.isRequired,
    stars: PropTypes.number.isRequired,
    size: PropTypes.string,
    id: PropTypes.string,
    className: PropTypes.string,
    onClick: PropTypes.func,
    disabled: PropTypes.bool,
};

export default withStyles(styles, { withTheme: true })(Stars);
